:root {
  --bg-light: #f4f4f6;
  --text-light: #333;
  --bg-dark: #1a1a1a;
  --text-dark: #f5f5f5;
  --accent: #7c3aed;
}

body, html, .app-container {
  margin: 0;
  padding: 0;
  font-family: 'Segoe UI', sans-serif;
  background-color: var(--bg-light);
  color: var(--text-light);
  min-height: 100vh;
  transition: all 0.2s ease;
}

.app-container.dark {
  background-color: var(--bg-dark);
  color: var(--text-dark);
}

.app-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 40px;
  background: linear-gradient(90deg, #7c3aed, #a855f7);
  color: white;
  border-radius: 10px;
  margin: 30px auto;
  max-width: 1100px;
}

.toggles button {
  margin-left: 12px;
  background: white;
  color: #7c3aed;
  border: none;
  padding: 10px 16px;
  border-radius: 8px;
  font-weight: bold;
  cursor: pointer;
}

.status-bar {
  max-width: 1100px;
  margin: 0 auto 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-inline: 40px;
}

.live {
  color: green;
}
.offline {
  color: red;
}

.content.single {
  display: flex;
  justify-content: center;
  padding: 20px;
}

.content.dual {
  display: flex;
  justify-content: space-around;
  gap: 20px;
  padding: 20px;
}

.video-box, .notepad-box {
  flex: 1;
  background: white;
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.05);
}

.app-container.dark .video-box,
.app-container.dark .notepad-box {
  background-color: #262626;
  color: white;
}

.video-box img {
  width: 100%;
  border-radius: 10px;
}

.notepad-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.notepad-box textarea {
  width: 100%;
  height: 300px;
  margin-top: 10px;
  padding: 12px;
  font-size: 1rem;
  border-radius: 8px;
  border: 1px solid #ccc;
  resize: vertical;
  background-color: white;
  color: black;
}

.app-container.dark .notepad-box textarea {
  background-color: #1a1a1a;
  color: white;
  border: 1px solid #444;
}
